import {useState} from "react";
import {Button, Modal, OverlayTrigger, Tooltip} from "react-bootstrap";


const PortfolioItem = (props) => {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const buildStack = (stackName, stack) => {
        return (
            <div className="row no-gutters">
                <div className="h3 col-12">
                    {stackName}
                </div>
                {stack.map((url, key) =>
                    <OverlayTrigger
                        overlay={
                            <Tooltip id={`tooltip`}>
                                {url.split('.')[0].replaceAll('_', ' ')}
                            </Tooltip>
                        }
                        className="col"
                    >
                        <img key={key} alt={url} className="stack-logos mb-2 mr-3"
                             src={process.env.PUBLIC_URL + '/img/logos/' + url}/>
                    </OverlayTrigger>
                )}
            </div>
        )
    }

    return (
        <>
            <div className="portfolio-item mx-auto" onClick={handleShow}>
                <div
                    className="portfolio-item-caption d-flex align-items-center justify-content-center h-100 w-100">
                    <div className="portfolio-item-caption-content text-center text-white"><i
                        className="fas fa-plus fa-3x"/></div>
                </div>
                <img className="img-fluid" src={process.env.PUBLIC_URL + props.imgUrl} alt=""/>
            </div>

            <Modal size="lg" show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{props.name}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="d-flex">
                        <img className="modal-logo mr-2" src={process.env.PUBLIC_URL + props.imgUrl} alt=""/>
                        <p className="flex-fill">
                            {props.description}
                        </p>
                    </div>
                </Modal.Body>
                <div className="mx-4 mb-4">
                    {buildStack('Backend', props.backEndStack)}
                </div>
                <div className="mx-4 mb-4">
                    {buildStack('Frontend', props.frontEndStack)}
                </div>
                <Modal.Footer>
                    <Button className="m-2" target="_blank" href="https://app.kalendario.ie/landing">Website</Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default PortfolioItem;
