const About = () => {
    const languages = ['Python', 'C#', 'Ruby', 'Javascript']
    const technologies = ['Django', 'Django Rest Framework', 'Ruby On Rails', '.Net Core', 'Angular', 'React', 'Redux', 'SQL']
    const other = ['SOLID Principles', 'Dry Coding', 'API']
    return (
        <section className="page-section bg-primary text-white mb-0" id="about">
            <div className="container">
                <h2 className="page-section-heading text-center text-uppercase text-white">Skills</h2>
                <div className="divider-custom divider-light">
                    <div className="divider-custom-line"></div>
                    <div className="divider-custom-icon"><i className="fas fa-star"></i></div>
                    <div className="divider-custom-line"></div>
                </div>
                <div className="row text-center">
                    <div className="col-lg-4 ml-auto">
                        {languages.map((i, k) => <p key={k} className="lead">{i}</p>)}
                    </div>
                    <div className="col-lg-4 ml-auto">
                        {technologies.map((i, k) => <p key={k} className="lead">{i}</p>)}
                    </div>
                    <div className="col-lg-4 mr-auto">
                        {other.map((i, k) => <p key={k} className="lead">{i}</p>)}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default About;
