import React, {Component} from 'react';

class Navbar extends Component {

    constructor(props) {
        super(props);
        this.state = {navClass: ''}
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        const shrinkNav = document.body.scrollTop > 100 || document.documentElement.scrollTop > 100;
        let navClass = 'navbar navbar-expand-lg bg-secondary text-uppercase fixed-top';
        if (shrinkNav) {
            navClass += ' navbar-shrink';
        }
        this.setState({navClass});
    }

    render() {

        return (
            <nav className={this.state.navClass} id="mainNav">
                <div className="container">
                    <img className="nav-image" src={process.env.PUBLIC_URL + '/img/navbar.png'} alt=""/>
                    <a className="navbar-brand js-scroll-trigger" href="#page-top"/>
                    <button
                        className="navbar-toggler navbar-toggler-right text-uppercase font-weight-bold bg-primary text-white rounded"
                        type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive"
                        aria-expanded="false" aria-label="Toggle navigation">
                        Menu
                        <i className="fas fa-bars"/>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarResponsive">
                        <ul className="navbar-nav ml-auto" >
                            <li className="nav-item mx-0 mx-lg-1"><a
                                className="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger" href="#portfolio">Portfolio</a>
                            </li>
                            <li className="nav-item mx-0 mx-lg-1"><a
                                className="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger" href="#about">About</a></li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }

}

export default Navbar;
