

const Header = () => (
    <header className="masthead bg-primary text-white text-center">
        <div className="container d-flex align-items-center flex-column">
            <img className="masthead-avatar mb-5" src={process.env.PUBLIC_URL + '/img/avatar.jfif'} alt=""/>
            <h1 className="masthead-heading text-uppercase mb-0">Gustavo Francelino</h1>
            <div className="divider-custom divider-light">
                <div className="divider-custom-line"/>
                <div className="divider-custom-icon"><i className="fas fa-star"/></div>
                <div className="divider-custom-line"/>
            </div>
            <p className="masthead-subheading font-weight-light mb-0">Full Stack Developer</p>
        </div>
    </header>
)

export default Header;
